.translate-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .header {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 55px;
    padding-bottom: 60px;
  }
  .inputs {
    padding-top: 50px;
    display: flex;
    align-items: center;
    .origin, .destination {
      width: 50%;
      padding: 24px;
      input {
        width: 100%;
        padding: 40px;
      }
    }
  }
  textarea {
    display: block;
    width: calc(100% - 4px);
    height: calc(100% - 20px - 8px);
    margin: 2px 0;
    padding: 10px;
    border: 1px solid;
    resize: none;
    border-radius: 10px;
  }
  .spinner {
    min-width: 45px;
    i {
      font-size: 40px;
    }
  }
}

@media only screen and (max-width: 600px) {
  .translate-container {
    .inputs {
      flex-direction: column;
      .origin, .destination {
        width: 100%;
      }
    }
  }
  .spinner {
    min-height: 45px;
  }
}
